@import url(https://fonts.googleapis.com/css2?family=Lemonada:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

:root {
  --primary-color-- : #89c74a; 
}

html{
  font-size: 62.2%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.image_conatiner{
    width: 100%;
    position: relative;
}

.image_conatiner .bannerimage{
    width: 100%;
}

.image_conatiner .bannertext{
    position: absolute;
    top: 0;
    height: 100%;
    left: 53%;
    display: flex;
    flex-direction: column;
    justify-content: center
}

.image_conatiner .bannertext img{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.image_conatiner .bannertext h3{
    font-size: 1.9rem;
    margin-top: 12px;
    font-weight: 400;
}

.image_conatiner .bannertext h1{
    font-size: 5rem;
    color: var(--primary-color--);
    font-family: 'Lemonada', cursive;
    line-height: 1.2;
}


.image_conatiner .bannertext .shopnow{
    margin-top: 18px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
    background: white;
    cursor: pointer;
}

.reccomded{
    width: 70%;
    margin: auto;
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 30px;
    gap: 30px;
}

.reccomded .rc1{
    width: 40%;
    position: relative;
    overflow: hidden;
    min-width: 300px;
}

.reccomded .rc1 img{
    width: 100%;
}

.reccomded .rc1 .rcdesc{
    position: absolute;
    top: -25%;
    left: 50%;
    width: 100%;
    height: 150%;
    background: pink;
    border-radius: 133px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.reccomded .rc1 .rcdesc .heading{
    margin-left: 10%;
    font-size: 2rem;
    font-weight: 700;
    width: 40%;
    font-family: 'Lemonada', cursive;
}

.reccomded .rc1 .rcdesc .desc{
    font-size: 1.7rem;
    margin-left: 10%;
    width: 40%;
    margin-top: 10px;
}

.reccomded .rc1 .rcdesc .buy{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 6px 10px;
    font-size: 1.1rem;
    border-radius: 40px;
    background-color: orange;
    border: none;
    color: white;
    margin: 10px 0px 0px 10%;
}

.reccomded .rc1 .rc2{
    background-color: #ffdb4f;
}

@media (max-width: 900px) {
    .image_conatiner .bannertext{
        left: 0;
        background: #ffffff7a;
        width: 100%;
        align-items: center;
    }
}

@media (max-width: 600px) {
    .image_conatiner .bannertext img{
        width: 30px;
    }
    .image_conatiner .bannertext h3{
        font-size: 1.2rem;
        margin-top: 5px;
    }
    .image_conatiner .bannertext h1{
        font-size: 3rem;
        margin-top: 5px;
    }
    .image_conatiner .bannertext .shopnow{
        font-size: 1.3rem;
        margin-top: 5px;
    }
}
.header_strip {
  display: flex;
  padding: 6px 0px;
  background: var(--primary-color--);
  align-items: center;
  justify-content: space-around;
  color: white;
}

.header_strip .left svg{
    margin-right: 6px;
}

.header_strip .left {
  display: flex;
  align-items: center;
  height: 30px;
  font-size: 1.4rem;
}

.header_strip svg {
  font-size: 1.7rem;
}


.header_strip .left .line , .header_strip .right .line{
  height: 90%;
  width: 2px;
  background: white;
  margin: 0px 13px;
  opacity: 0.7;
}

.header_strip .left .call {
  -webkit-transform: rotateZ(-100deg);
          transform: rotateZ(-100deg);
}

.header_strip .right {
  display: flex;
  width: 20%;
  width: 300px;
  justify-content: space-between;
  align-items: center;
  height: 30px;
}

.header_strip .right #country{
    background: none;
    border: none;
    outline: none;
    color: white;
    font-weight: 700;
}

.header_strip .right #country option{
    background-color: var(--primary-color--);
}
.header_middle{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0px;
}

.header_middle .navigation{
  width: 47%;
  min-width: 530px;
  display: flex;
  list-style: none;
  align-items: center;
  justify-content: space-between;
  margin: 0px 40px 0px 70px;
}

.header_middle .toggle{
  display: none;
}

.header_middle .navigation li{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.7rem;
  font-family: 'Oswald', sans-serif;
  cursor: pointer;
}

.header_middle .navigation li:first-child{
  color: var(--primary-color--);
}

.header_middle .user_action{
  display: flex;
  width: 90px;
  align-items: center;
  justify-content: space-between;
}

.header_middle .user_action svg{
  font-size: 2rem;
}

.header_bottom{
  width: 70%;
  margin: auto;
  min-width: 850px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.5rem;
  height: 40px;
  padding: 20px 0px;
}

.header_bottom .all_departments{
  display: flex;
  width: 23%;
  align-items: center;
  justify-content: space-evenly;
  background: var(--primary-color--);
  color: white;
  height: 100%;
}

.header_bottom .search_container{
  display: flex;
  width: 72%;
  align-items: center;
  height: 100%;
  border: 1px solid rgba(128, 128, 128, 0.795);
  position: relative;
}
.header_bottom .search_container .all_catagories{
  display: flex;
  width: 25%;
  align-items: center;
  justify-content: space-evenly;
}

.header_bottom .search_container .line{
  height: 57%;
  width: 2px;
  background: gray;
}


.header_bottom .search_container #search{
  margin-left: 4%;
  width: 71%;
  height: 100%;
  border: none;
  outline: none;
}

.header_bottom .search_container button{
    border: none;
    outline: none;
    height: 41px;
    position: absolute;
    right: 0;
    padding: 0px 20px;
    background: var(--primary-color--);
    color: white;
}

@media (max-width : 900px) {
  .header_middle{
    justify-content: space-around;
  }
  .header_middle .navigation{
    display: none;
  }
  .header_middle .toggle{
    display: block;
  }
  .header_bottom .all_departments{
    display: none;
  }
  .header_bottom {
    justify-content: center;
    min-width: 500px;
  }
  .header_bottom .search_container{
    width: 100%;
  }
}

@media (max-width : 650px) {
  .header .header_strip{
    flex-direction: column;
  }
}

@media (max-width : 650px) {
  html{
    font-size: 48.5%;
  }
  .header_bottom{
    width: 100%;
    min-width: 300px;
  }

}
.product_container{
    margin-top: 30px;
}

.product_container .head{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: center;
}

.product_container .head h2{
    font-size: 2.5rem;
    color: var(--primary-color--);
    font-family: 'Lemonada', cursive;
}

.product_container .head p{
    margin-top: 11px;
    font-size: 1.6rem;
}

.product_action{
    width: 70%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit , minmax(100px , 1fr));
    place-items: center;
    margin-top: 50px;
    grid-gap: 10px;
}

.product_action .pa1{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   width: 100%;
   height: 100%;
}

.allproducts{
    width: 95%;
    display: grid;
    grid-template-columns: repeat(auto-fit , minmax(200px , 1fr));
    grid-gap: 25px;
    margin: 0px auto;
    margin-top: 100px;
}

.allproducts .card{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
    border: 1px solid gray;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
}

.allproducts .card .cardimage{
    width: 100%;
    display: flex;
    justify-content: center;
}

.allproducts .card .cardimage img{
    width: 80%;
}

.allproducts .card .quantity{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.allproducts .card .quantity .quan{
    padding: 4px 12px;
    background: #ff6600;
    border-radius: 12px;
    font-size: 1.3rem;
    color: white;
}

.allproducts .card .cardname{
    font-size: 2rem;
    font-weight: 700;
}

.allproducts .card .price{
    display: flex;
    justify-content: space-evenly;
    font-size: 1.5rem;
    font-weight: 800;
    width: 100%;
}

.allproducts .card .price .current{
    color: var(--primary-color--);
}

.allproducts .card .price .previous{
    font-weight: 500;
    color: gray;
    text-decoration: line-through;
}

.allproducts .card .add{
    padding: 7px 15px;
    border: none;
    outline: none;
    background: var(--primary-color--);
    border-radius: 30px;
    margin-bottom: 20px;
    margin-top: 5px;

}

.saletag{
    position: absolute;
    top: 1px;
    left: -1px;
    padding: 4px 6px;
    background: black;
    color: white;
    font-size: 1.2rem;
}

.savetag{
    position: absolute;
    top: -1px;
    right: -1px;
    padding: 4px 6px;
    background: black;
    color: white;
    font-size: 1.2rem;
}
